
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import Vue from 'vue'
export default {
  props:{

  },
  name: 'index-menu',
  components:{
    Cell,
    Group,
    CellBox
  },
  data () {
    return {
      showContent001: false,
      menuOpts:[
        {
          text:"飞行准备",
          id:0,
          value:"flightPlan",
          level:1,
          hasChild:false,
          selected:true
        },
        {
          text:"通知公告",
          id:1,
          value:"importInfo",
          level:1,
          hasChild:false,
          selected:false
        },
        {
          text:"飞行计划查询",
          id:2,
          value:"flightFlan",
          level:1,
          hasChild:false,
          selected:false
        },
        {
          text:"航班动态查询",
          id:3,
          value:"flightDynamic",
          level:1,
          hasChild:false,
          selected:false
        },
        {
          text:"机场信息",
          id:4,
          value:"airport",
          level:1,
          hasChild:false,
          selected:false
        },
        {
          text:"个人信息",
          id:5,
          value:"personQuery",
          level:1,
          hasChild:true,
          selected:false,
          child:[
            {
              text:"个人信息",
              id:6,
              value:"personInfo",
              level:2,
              hasChild:false,
              selected:false
            },
            {
              text:"飞行小时",
              id:7,
              value:"flightHours",
              level:2,
              hasChild:false,
              selected:false
            },
            {
              text:"QAR数据",
              id:8,
              value:"QARData",
              level:2,
              hasChild:false,
              selected:false
            }
          ]
        }
      ]
    }
  },
  computed:{

  },
  methods:{
    clickTest(event){
      console.log(event.currentTarget.dataset.id);
    },
    addSelectBack(event){
      let currentID=event.currentTarget.dataset.id;
      console.log(currentID);
      /*this.menuOpts[event.currentTarget.dataset.id].*/
      this.menuOpts.map((val)=>{
        if(val.hasChild==true){
          val.child.map((childVal)=>{
            childVal.selected=false;
            if(childVal.id==currentID){
              Vue.set(childVal,'selected',true)
            }
            return val
          });

        }else{
          val.selected=false;
          if(val.id==currentID){
            Vue.set(val,'selected',true)
          }
          return val;
        }
      });
      this.$emit('menuClick');
    }
  }
}



