import Vue from 'vue'
export default {
  props:{
    isMaskShowStatus:Boolean
  },
  name: 'vux-mask-ycy',
  components:{

  },
  data () {
    return {
      zIndex:-1

    }
  },
  computed:{
    isShow(){
      return this.isMaskShowStatus;
    },
  },
  watch:{
    isShow(val){
      if(val===true){
        this.zIndex=500;
      }
      setTimeout(()=>{
        if(val===false){
          this.zIndex=-1;
        }
      },200)
    }
  },
  methods:{
    closeMask(){
      this.$emit("closeMask")
    }
  }
}



